body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@media (min-width: 900px) {
  .limiter {
    min-width: 700px;
    /*width: 80%;*/
    padding: 50px 100px;
  }
}

@media (max-width: 899px) {
  .limiter {
    width: 100%;
    padding: 0 !important;
  }

  .input-container {
    margin: 0px auto 20px auto;
    width: 100% !important;
  }

  .table_center {
    width: 100% !important;
  }

  .table_center td:first-child {
    padding-top: 20px !important;
  }

  .table_center td:last-child {
    padding-bottom: 20px !important;
  }

  .table_center td {
    /*padding-left: 40px !important;*/
    padding: 10px 15px 10px 15px !important;
    width: auto;
    display: block;
    text-align: left !important;
  }

  .table_center thead {
    display: none;
  }

  input[type="text"] {
    padding: 10px 15px !important;
  }
}

a { color: gray; }

.table_center {
  font-family: "Comic Sans MS";
  color: #808080;

  width: 795px;


  margin: 0 auto;

  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -o-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -ms-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);

  border-collapse: collapse;

}

.table_center thead {
  box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  -o-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
  -ms-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
}

.table_center th {
  font-size: 18px;
  color: #fa4251;
  line-height: 1.4;
  background-color: transparent;

  padding-top: 18px;
  padding-bottom: 18px;

  text-align: left;
}

.table_center td{
  padding: 15px 0px;
}

.table_center tr {
  border-bottom: 1px solid #f2f2f2 !important;
}

.table_center .column1 {
  padding-left: 40px;
  width: 300px;
}

.table_center .column2 {
  width: 200px;
}

.table_center .column3 {
  width: 250px;
}

td.right, th.right {
  text-align: right;
  padding-right: 50px;
}

.input-container {
  margin: 0px auto 20px auto;
  width: 795px;

  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -o-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
  -ms-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);

  border-collapse: collapse;
}

input[type="text"] {
  padding: 10px 36px;
  width: 100%;
  font-family: "Comic Sans MS";
  font-size: 18px;
  border: none;
}

.cell100 {
  white-space: nowrap;
  overflow: hidden;
  /*text-overflow: ellipsis;*/
}


